*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.section{
  height: calc(100vh - 61px);
}
nav{
  position: fixed;
  bottom: 0;
  width: 100vw;
  background-color: rgb(135, 112, 203);
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 10px;
  z-index: 100;
}

nav .bottom-link{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: auto;
}
.hide{
  background-color: #e9e9e9;
  color: black;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 80vw;
  height: 100%;
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.hide ul{
  list-style-type: none;
}

.hide ul li{
  display: block;
  text-transform: capitalize;
  padding: 10px;
}

.hero{
  position: relative;
  z-index: -100;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(./images/hero-img.jpg);
  background-size: cover;
  background-position: top;
}


.section .hero .header h1{
  font-size:2.3rem;
  font-weight: bolder;
}
.section .hero .header p{
  font-weight: 500;
}
.section .hero .header{
  margin:0 1.5rem;
}

.hero .nav-links{
  position: absolute;
  bottom: 10px;
  left :"50%";
  transform:translate(50% -10px);
  display:flex;
  align-items: center;
}
.hero .nav-links span{
  width: 25px;
  height: 25px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}
.hero .nav-links span:nth-of-type(even){
  margin: 0 25px;
}
.hero .nav-links span:nth-of-type(odd){
  background-color: white;
  width: 15px;
  height: 15px;
}



.who-we-are{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #B2207D;
  padding: 0 15px;
}
.who-we-are .question-mark{
  transform:rotate(-42deg);
  color: #D98234;
  width: 100px;
  height: 100px;
}
.who-we-are h1{
  font-size: 2.3rem;
}


.admission{
  height:auto;
  background-color: #5C37B2;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.admission h1{
  font-size: 1.3rem;
  text-align: center;
}
.admission .box{
  margin: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90vw;
  height: 180px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-radius: 10px;
}
.admission .box .bg{
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-color: rgb(153, 153, 153, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
}
.admission .box .bg .overlay-text{
  font-size: 1.5rem;
  font-weight: bolder;
} 
.admission .creche{
background-image: url(./images/creche.jpg);
}
.admission .nursery{
background-image: url(./images/nursery.jpg);
}
.admission .primary{
background-image: url(./images/primary.jpg);
}
.admission .secondary{
background-image: url(./images/secondary.jpg);
}



.testimony{
background-color: #B2207D ;
/* margin-bottom: 61px; */
position: relative;
}
.testimony h1{
  text-align: center;
}
.testimony > .testimonies{
  display: flex;
  align-items: center;
  flex-direction: column;
}
.testimony .testifier-img{
  border-radius: 50%;
  width: 130px;
  height: 130px;
}
.testimony > .testimonies > p{
 text-align: center;
}
.testimony .nav-links{
  position: absolute;
  bottom: 50px;
  left :50%;
  transform:translate(-50% -50%);
  display:flex;
  align-items: center;
}
.testimony .nav-links span{
  width: 25px;
  height: 25px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}
.testimony .nav-links span:nth-of-type(even){
  margin: 0 25px;
}
.testimony .nav-links span:nth-of-type(odd){
  background-color: white;
  width: 15px;
  height: 15px;
}